.a {
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 100%;
}

.a-card-bg {
    background-color: #393737;
    position: absolute;
    top: 50px;
    left: 50px;
    z-index: -1;
}

.a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    overflow: hidden;

}

.a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 30px;

}

.a-right {
    flex: 1;
}

.a-title {
    font-weight: 400;

}

.a-sub-title {
    margin: 20px 0px;
}

.a-desc {
    font-weight: 300;
}

.a-award {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.a-award-img {
    width: 170px;
    height: 170px;
    border-radius: 20px;
}

.a-award-desc {
    width: 70%;
}

.a-award-title {
    color: gray;
    font-weight: bold;
    margin: 10px 0;
}

@media screen and (max-width: 480px) {
    .a {
        flex-direction: column;
        align-items: center;
        margin-top: 15px;
    }
    .a-right {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        padding: 20px;
    }

    .a-left {
        width: 100%;
    }
    .a-card {
        height: 30vh;
    }
    .a-award{
        display: none;
    }
}

